import React from "react";
import classNames from "classnames";
// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
import styles from "./index.module.scss";
import { Link } from "react-router-dom";

const DefaultCard = ({
  withButton = false,
  header = "الغردقه",
  subHeader = "من عند 350 ج.م من عند 350 ج.م من عند 350 ج.م",
  title = "1990ج",
  image = "https://source.unsplash.com/random?hotel",
  tripId,
  isHajj,
}) => {
  const tripType = isHajj ? "hajj" : "umra";

  let callToActionClasses = classNames({
    [styles["call-to-action"]]: true,
    [styles["padding"]]: withButton,
  });

  return (
    <div className={styles["card"]}>
      <img src={image} alt="Avatar" className="image" />
      <div className={styles["content"]}>
        <div className={styles["info"]}>
          <div className={styles["header"]}>
            <h4> {header}</h4>
            <span>{subHeader}</span>
          </div>

          <div className={callToActionClasses}>
            <div className={styles["title"]}>
              <p> {title}</p>
            </div>
            {withButton && (
              <div className={styles["button"]}>
                <Link to={`/trips/${tripType}/${tripId}`}>
                  <button>تفاصيل</button>
                </Link>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default DefaultCard;
