import HeroSection from "components/shared/HeroSection/HeroSection";
import React from "react";

import { Container } from "react-bootstrap";

//asests
import tourism1 from "assets/images/tourism-1.jpg";

// styles
import styles from "./index.module.scss";
import HotelsSlider from "./HotelsSlider/HotelsSlider";
import BlueButton from "components/shared/BlueButton/BlueButton";
import TourismCard from "./TourismCard/TourtismCard";

const TourismDepartmentPage = () => {
  return (
    <>
      {/* <div className={styles[""]}> */}
      <HeroSection
        title={"قسم السياحه"}
        description={
          "نوفر لك كل خدمات السياحة من حجز تذاكر، فنادق و رحلات سياحية"
        }
        image={tourism1}
        bodyClass={styles["hero-image"]}
      />

      <div className={styles["tourism-content"]}>
        <Container>
          <HotelsSlider />
          <BlueButton />

          <TourismCard
            image={"https://source.unsplash.com/random?makkah"}
            title={"رحلات العمرة"}
            subTitle={
              "أستمتع بمناسك العمرة عندالحجز لرحلة العمرة, احجز معنا الان تأشيرة العمرة بأفضل الفنادق واقل الاسعار, استمتع بصفة العمرة مع شركة فلاينج كاربت للسياحة"
            }
          />
        </Container>
      </div>

      {/* </div> */}
    </>
  );
};

export default TourismDepartmentPage;
