import React from "react";
import { Col, Container, Row } from "react-bootstrap";

const HeroSection = ({
  title,
  description,
  image,
  extraElement,
  bodyClass,
}) => {
  return (
    <div
      className={bodyClass}
      style={image && { backgroundImage: `url(${image})` }}
    >
      <Container>
        <Row>
          <Col xs={12}>{title && <h3>{title}</h3>}</Col>
          <Col xs={12}>{description && <p>{description}</p>}</Col>
          {extraElement}
        </Row>
      </Container>
    </div>
  );
};

export default HeroSection;
